.page-partner {
  h2 {
    font-size: pxToVW(30);
    line-height: pxToVW(20);
  }

  .form-search {
    width: 100%;
    padding-top: pxToVW(30);

    &__left {
      display: flex;

      .customer-select {
        margin-right: pxToVW(19);
      }

      .select-headquarters {
        width: pxToVW(135);
      }

      .select-job {
        width: pxToVW(161);
      }

      .select-vote {
        width: pxToVW(137);
      }

      .select-action {
        width: pxToRem(95);
        font-size: pxToRem(15);
      }

      button.button.btn {
        height: pxToVW(40) !important;
        padding: pxToVW(7) pxToVW(4) pxToVW(6);
        font-size: pxToVW(15);
        line-height: pxToVW(18);
        background: #2a5299;
        border-radius: pxToVW(5);
      }

      .react-datepicker-wrapper {
        margin-right: pxToVW(15);

        .react-datepicker__input-container {
          input {
            border: pxToVW(1) solid $color-border;
            line-height: pxToVW(36);
            display: block;
            font-size: pxToVW(16);
            padding-left: pxToVW(10);
            // padding: pxToVW(15);
            // color: $color-input;
            background-color: rgba(209, 209, 209, 0.87);
            border-radius: pxToVW(5);
            width: 100%;
            letter-spacing: pxToVW(-0.25);
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            font-weight: $regular;
          }
        }
      }
    }

    &__right {
      display: flex;
      justify-content: flex-end;

      button.button.btn {
        height: pxToVW(39) !important;
        padding: pxToVW(7) pxToVW(4) pxToVW(6);
        font-size: pxToVW(15);
        line-height: pxToVW(18);
        background: #2a5299;
        border-radius: 0;
        margin-left: pxToVW(-2);
        z-index: 999;
      }

      .input__box input {
        border: 0;
        width: pxToVW(370);
      }
    }
  }

  .table-partner {
    margin-top: pxToVW(25);

    tr {
      td {
        &:nth-of-type(4) {
          color: $color-link-table;
        }

        // &:nth-of-type(5){
        //   display: none;
        // }
      }
    }
  }
}

.action-delete {
  text-align: right;
  padding-top: pxToVW(20);

  button.button.btn {
    height: pxToVW(40) !important;
    padding: pxToVW(7);
    font-size: pxToVW(15);
    line-height: pxToVW(18);
    background: $color_primary;
    border-radius: 0;
    margin-left: pxToVW(-2);
    z-index: 999;
  }
}

.wrapper-pagination {
  .pagination {
    justify-content: flex-end;

    .page-item {
      background-color: $white;

      .page-link {
        color: $color-black;
        font-size: pxToVW(15);
        border: pxToVW(1) solid $bg-light5;

        &:focus {
          outline: none;
          box-shadow: unset;
        }
      }

      &.active {
        .page-link {
          background-color: $color_primary;
          border-color: unset;
          color: $white;

          &:focus {
            outline: none;
            box-shadow: unset;
          }
        }
      }
    }
  }
}

.avatar {
  width: pxToVW(181);
  height: pxToVW(181);
  border-radius: 50%;
  background-color: $bg-color;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;

  p {
    font-size: pxToVW(15);
    line-height: pxToVW(18);
    color: $white;
    font-weight: $medium;
    padding: pxToVW(14) 0;
    background: rgb(42, 82, 153);
    background: linear-gradient(-90deg, rgba(42, 82, 153, 1) 0%, rgba(21, 41, 77, 1) 100%);
    text-align: center;
    position: absolute;
    bottom: 0;
    width: 100%;
  }
}

.page-partner-management {
  .input__label {
    font-size: pxToVW(20) !important;
    margin-bottom: pxToVW(10);
  }

  .input-change {
    background-color: $white;
    color: $color-black;
    border: pxToVW(2) solid $color-border;
    margin-bottom: pxToVW(20);
  }

  .customer-select {
    color: $color-black;
    margin-bottom: pxToVW(20);

    .input__label {
      margin-bottom: pxToVW(10);
    }

    .input__box {
      background-color: $white;
    }
  }

  .button.button.btn {
    font-size: pxToVW(15);
    font-weight: $medium;
    line-height: pxToVW(18);
    background: $color_primary;
    border-radius: unset;
    margin-top: pxToVW(25);
    height: pxToVW(40);
  }

  .table {
    tr td:nth-of-type(4) {
      span {
        color: $white;
        background-color: $color-tag;
        padding: pxToVW(5) pxToVW(10);
        border-radius: pxToVW(10);
        font-size: pxToVW(15);
      }
    }
  }

  .partner__tab {
    border-bottom: unset;
    padding-left: pxToVW(15);
    padding-top: pxToVW(85);

    .nav-link {
      color: $color-black;
      font-size: pxToVW(20);
      line-height: pxToVW(24);
      padding: 0;
      margin-right: pxToVW(20);
      text-transform: uppercase;

      &.active {
        color: $color_primary;
        background-color: transparent;
        border-color: transparent;
        padding-bottom: pxToVW(5);
        border-bottom: pxToVW(2) solid $color_primary;
      }
    }
  }

  .tab-content {
    width: 100%;
  }
}

.list-product {
  display: inline-block;
  white-space: nowrap;
  overflow: auto;

  .product {
    width: pxToVW(284);
    height: pxToVW(279);
    border-radius: pxToVW(10);
    background-color: $white;
    margin-right: pxToVW(4);
    position: relative;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: cover;

    p {
      font-size: pxToVW(15);
      line-height: pxToVW(18);
      color: $white;
      padding-top: pxToVW(28);
      padding-bottom: pxToVW(27);
      background-color: $color_primary;
      text-align: center;
      position: absolute;
      bottom: 0;
      width: 100%;
      border-bottom-left-radius: pxToVW(10);
      border-bottom-right-radius: pxToVW(10);
      white-space: pre-wrap;
    }
  }

  .add-product {
    width: pxToVW(284);
    height: pxToVW(279);
    border-radius: pxToVW(10);
    background-color: $color-box-add;
    font-size: pxToVW(200);
    font-weight: $medium;
    color: $color-add;
    padding-left: pxToVW(85);
    padding-top: pxToVW(25);
    display: inline-block;
    vertical-align: top;
  }

  &__back {
    display: flex;
    align-items: center;
    padding-bottom: pxToVW(40);

    img {
      transform: rotate(90deg);
      width: pxToVW(13);
    }

    &__title {
      font-size: pxToVW(20);
      line-height: pxToVW(24);
      color: $color_primary;
      font-weight: $bold;
      padding-left: pxToVW(15);
    }
  }
}

.box-info-partner {
  h2 {
    font-size: pxToVW(20);
    line-height: pxToVW(24);
    font-weight: $medium;
    color: $color-black;
    padding-bottom: pxToVW(10);
  }

  h1 {
    font-size: pxToVW(30);
    line-height: pxToVW(20);
    font-weight: $bold;
    color: $color_primary;
    padding-bottom: pxToVW(25);
  }

  p {
    font-size: pxToVW(15);
    line-height: pxToVW(20);
    font-weight: $medium;
    color: $color_primary;
    padding-top: pxToVW(12);
  }

  h4 {
    font-size: pxToVW(15);
    line-height: pxToVW(20);
    font-weight: $medium;
    color: $color_primary;
    padding-bottom: pxToVW(25);
  }

  .list-tag {
    padding-top: pxToVW(5);

    span {
      font-size: pxToVW(15);
      line-height: pxToVW(24);
      color: $white;
      padding: pxToVW(5) pxToVW(6);
      background-color: $color-tag;
      border-radius: pxToVW(15);
      margin-right: pxToVW(8);
    }
  }
}

.popup-add-product {
  .title-content {
    display: flex;
  }

  .modal-content {
    padding: 0;

    &__iconClose {
      top: pxToVW(-10);
      right: pxToVW(-10);
      z-index: 999;
    }

    .modal-body {
      padding: 0;
    }
  }

  .modal-dialog {
    max-width: pxToVW(1100);
  }

  &__left {
    width: pxToVW(720);
    background-color: $bg-file-popup;

    .box__input {
      padding: pxToVW(13);
      text-align: center;
      width: 100%;
      height: 100%;
      padding-top: pxToVW(100);

      .box__file {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1;
      }

      label {
        font-size: pxToVW(15);
        line-height: pxToVW(30);
        color: $color-placeholder;
      }

      .add-file {
        width: pxToVW(150);
        background: $color_primary;
        height: pxToVW(35);
        border-radius: 0;
        text-align: center;
        display: block;

        p {
          color: $white;
          text-align: center;
        }
      }
    }
  }

  &__right {
    width: pxToVW(380);
    padding: pxToVW(20);
    text-align: right;

    button {
      width: pxToVW(150);
      background: $color_primary !important;
      height: pxToVW(35) !important;
      border-radius: 0 !important;
      text-align: center;
      display: block;
      margin-top: pxToVW(48) !important;
      float: right;

      p {
        color: $white;
        text-align: center;
      }
    }
  }

  p {
    text-align: left;
    font-size: pxToVW(15);
    color: $color-black;
    font-weight: $medium;
    padding-bottom: pxToVW(5);
  }

  .input__box {
    width: 100% !important;
  }

  .input__label {
    color: $color-black;
  }

  textarea {
    border-radius: pxToVW(5);
    padding: pxToVW(10) pxToVW(16);
    font-size: pxToVW(15);
    width: 100%;
    background-color: rgba(209, 209, 209, 0.87);
    ;
  }
}