.page-post{
  button.button.btn{
    height: pxToVW(47);
    font-size: pxToVW(20);
    padding-left: pxToVW(17);
    padding-right: pxToVW(17);
    &:first-child{
      margin-right: pxToVW(40);
    }
  }
  .action-delete{
    button.button.btn{
      margin-right: 0;
      font-size: pxToVW(15);
    }
  }
}
.page-register-post{
  .title-page{
    padding-bottom: pxToVW(38);
  }
  .input{
    &__label{
      font-size: pxToVW(20);
      line-height: pxToVW(24);
      font-weight: $medium;
      color: $color-black;
      margin-bottom: pxToVW(8);
    }
    &__box{
      input{
        background-color: $white;
      }
    }
  }
  textarea{
    background-color: $white;
    width: 100%;
    border: pxToVW(1) solid #e8e9ec;
    font-size: pxToVW(15);
    padding: pxToVW(10) pxToVW(7);
    &::placeholder{
      font-size: pxToVW(15);
    }
  }
  .customer-select {
    padding-top: pxToVW(38);
    
    .input__label{
      font-size: pxToVW(20);
      line-height: pxToVW(24);
      font-weight: $medium;
      color: $color-black;
      margin-bottom: pxToVW(8);
    }
    .input__box {
      background-color: $white;
    }
  }
  h4{
    font-size: pxToVW(10);
    line-height: pxToVW(13);
    color: $color-table;
    margin-bottom: pxToVW(28);
    padding-top: pxToVW(14);
  }
  p{
    font-size: pxToVW(20);
    line-height: pxToVW(24);
    font-weight: $medium;
    color: $color-black;
    margin-bottom: pxToVW(8);
  }
  .action-register{
    padding-top: pxToVW(100);
    button.button.btn{
      height: pxToVW(47);
      padding-left: pxToVW(17);
      padding-right: pxToVW(17);
      p{
        color: $white;
        font-size: pxToVW(20);
        margin-bottom: 0;
      }
    }
  }
  .action-delete{
    button.button.btn{
      p{
        color: $white;
        font-size: pxToVW(15);
      }
    }
  }
}

.page-register-post{
  .action-delete{
    .button{
      border-radius: pxToVW(5) !important;
    }
  }
  .input{
    padding-bottom: pxToVW(20);
  }
  .customer-select{
    padding-bottom: pxToVW(20);
  }
  .ck-editor{
    margin-bottom: pxToVW(20) !important;
  }
  .ck-content{
    height: pxToVW(250);
  }
  .thumbnail{
    background-color: $white;
    padding: pxToVW(25);
    border-radius: pxToVW(10);
    margin-bottom: pxToVW(28);
    .action-delete{
      text-align: right;
    }
    .image-thumbnail{
      width: 100%;
      height: pxToVW(230);
      margin-bottom: pxToVW(10);
    }
  }
  .box-image{
    width: 100%;
    height: pxToVW(230);
    background-color: #6f6f6f;
    margin-bottom: pxToVW(10);
  }
  .select-category{
    background-color: $white;
    border-radius: pxToVW(10);
    margin-bottom: pxToVW(28);
    padding: pxToVW(20) pxToVW(25);
    .css-yk16xz-control, .css-1pahdxg-control {
      border: pxToVW(1) solid #707070;
    }
  }
  .box-status{
    background-color: $white;
    padding: pxToVW(20);
    border-radius: pxToVW(10);
    p{
      font-size: pxToVW(15);
      line-height: pxToVW(18);
      font-weight: $medium;
      color: $color-black;
      span{
        color: $color_primary;
        text-decoration: underline;
      }
    }
    .group-action{
      display: flex;
      justify-content: flex-end;
      align-items: center;
      .view-post{
        font-size: pxToVW(15);
        line-height: pxToVW(18);
        font-weight: $medium;
        color: $color_primary;
      }
      .draft{
        font-size: pxToVW(15);
        line-height: pxToVW(18);
        font-weight: $medium;
        color: $color-black;
        opacity: 0.58;
        padding-right: pxToVW(27);
      }
      .button{
        height: pxToVW(39);
        width: pxToVW(153);
        border-radius: pxToVW(5);
        p{
          color: $white;
          margin-bottom: 0;
        }
      }
    }
    
  }
}